            var h = tanggal.getHours();
            var m = tanggal.getMinutes();
            var s = tanggal.getSeconds();
            m = this.checkTime(m);
            s = this.checkTime(s);
            this.waktu= h + ":" + m + ":" + s;
            console.log(this.waktu)
            var t = setTimeout(this.startTime, 600000); //500 1 detik, 60000 1 menit
            if(h>=15){
              let that = this;
