                  that.$store.state.mode=false 
                })
              }
            }
        },
        checkTime(i) {
            if (i < 10) {i = "0" + i};  // add zero in front of numbers < 10
            return i;
        },
    checkMode(){
      db.collection('tv-mode').onSnapshot(res=>{
        let data = res.docs.map(e=>{
          return {
            id:e.id,
            ...e.data()
          }
        })
        console.log(data[0].mode)
        if(!data[0].mode){
          this.$router.push('/info')
        }
      })
    },
    mulai() {
      setTimeout(() => {
        document.getElementById("myVideo").setAttribute("src", videos);
        document.getElementById("myVideo").load();
        document.getElementById("myVideo").play();
      }, 5000);
       let that =this
       db.collection('tv/btn/sukubunga').onSnapshot(res=>{
