         that.datasuku=data
       })
        db.collection('tv/btn/tvtext').onSnapshot(res=>{
         let data=res.docs.map(e=>e.data())
         that.texts=data
       })
         db.collection('tv/btn/gambar').onSnapshot(res=>{
         let data=res.docs.map(e=>e.data())
         that.gambars=data
       })
     
    },
    videoPlay(videoNum) {
      document
        .getElementById("myVideo")
        .setAttribute("src", this.vids[videoNum]);
      document.getElementById("myVideo").load();
      document.getElementById("myVideo").play();
    }
  },
  beforeMount() {
